//  local 
export function loadFromLocalStorage(name) {
  let data = window.localStorage.getItem(name);
  // (null)
  if (!data) {
    return {
      title: "",
      email: "",
      phone: "",
      option: "",
      howToKnow: "",
      suggest: "",
    };
  }
  return JSON.parse(data);
}
//  html  name 
export function getErrorMessage(inputName, inputValue) {
  switch (inputName) {
    case "title":
      if (!inputValue) {
        return "";
      } else if (!/^[\u4e00-\u9fa5_a-zA-Z0-9]+$/.test(inputValue)) {
        return "///";
      } else {
        return "";
      }
    case "email":
      if (!inputValue) {
        return "";
      } else if (
        !/^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/.test(
          inputValue
        )
      ) {
        return "";
      } else {
        return "";
      }
    case "phone":
      if (!inputValue) {
        return "";
      } else if (!/^09\d{8}$/.test(inputValue)) {
        return "";
      } else {
        return "";
      }
    case "option":
      if (!inputValue) {
        return "";
      } else {
        return "";
      }
    case "howToKnow":
      if (!inputValue) {
        return "";
      } else if (!/^[\u4e00-\u9fa5a-zA-Z]+$/.test(inputValue)) {
        return "/";
      } else {
        return "";
      }
    default:
      return "";
  }
}
