import { useInput } from "./useInput";
import { getErrorMessage } from "./utils";

export default function useForm() {
  //  hooks
  const {
    inputValue,
    setInputValue,
    handleInputChange,
    inputErrorMessage,
    setInputErrorMessage,
  } = useInput();

  // 
  const handleClickButtonSave = () => {
    localStorage.setItem("inputValue", JSON.stringify(inputValue));
  };

  // 
  const handleFormSubmit = (e) => {
    // 
    e.preventDefault();
    let hasRequiredInputEmpty = false;

    // 
    for (const key in inputValue) {
      // 
      if (inputValue[key] === "" && key !== "suggest") {
        hasRequiredInputEmpty = true;
      }
      // radio 
      if (key === "options" && inputValue[key] === "") {
        setInputErrorMessage((inputErrorMessage) => ({
          ...inputErrorMessage,
          [key]: getErrorMessage(key, inputValue[key]),
        }));
      }
      //  functional update 
      setInputErrorMessage((inputErrorMessage) => ({
        ...inputErrorMessage,
        [key]: getErrorMessage(key, inputValue[key]),
      }));
    }

    //  input  state  return 
    if (hasRequiredInputEmpty) {
      alert("");
      return;
    }

    // 
    for (const key in inputErrorMessage) {
      if (inputErrorMessage[key] !== "") {
        // eslint-disable-next-line
        alert("");
        return;
      }
    }
    // 
    // eslint-disable-next-line
    alert(` 
      
      ${inputValue.title}
      ${inputValue.email}
      ${inputValue.phone}
      ${inputValue.option}
      ${inputValue.howToKnow}
      ${inputValue.suggest}
    `);
  };

  //
  return {
    inputValue,
    setInputValue,
    handleInputChange,
    inputErrorMessage,
    handleClickButtonSave,
    handleFormSubmit,
  };
}
