import {
  ADD_TODO,
  DELETE_TODO,
  CLEAR_DONE_TODO,
  TOGGLE_TODO,
  UPDATE_TODO,
} from '../actionTypes';

//  id
let todoId = 0;
//  render 
let todoData = window.localStorage.getItem('todos');
if (todoData) {
  todoData = JSON.parse(todoData);
  //  id  local 
  todoId = todoData[0].id + 1;
} else {
  // (null)
  todoData = [];
}

const initialState = {
  todos: todoData,
};

export default function todosReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_TODO: {
      todoId += 1;
      return {
        // 
        ...state,
        todos: [
          // 
          {
            // 
            id: todoId,
            content: action.payload.content,
            isDone: false,
          },
          // 
          ...state.todos,
        ],
      };
    }
    case DELETE_TODO: {
      return {
        ...state,
        todos: state.todos.filter(todo => todo.id !== action.payload.id),
      };
    }
    case CLEAR_DONE_TODO: {
      return {
        ...state,
        //  isDone  false
        todos: state.todos.filter(todo => !todo.isDone),
      };
    }
    case TOGGLE_TODO: {
      return {
        ...state,
        todos: state.todos.map((todo) => {
          //  ID 
          if (todo.id !== action.payload.id) return todo;
          return {
            ...todo,
            isDone: !todo.isDone,
          };
        }),
      };
    }
    case UPDATE_TODO: {
      return {
        ...state,
        todos: state.todos.map((todo) => {
          //  ID 
          if (todo.id !== Number(action.payload.id)) return todo;
          //  ID  content
          return {
            ...todo,
            content: action.payload.content,
          };
        }),
      };
    }
    default: {
      return state;
    }
  }
}
