import { useState, useEffect, useRef, useCallback } from "react";
import useInput from "./useInput";

function writeTodosToLocalStorage(todos) {
  window.localStorage.setItem(
    "todos",
    todos.length === 0 ? [] : JSON.stringify(todos)
  );
}

export default function useTodos() {
  //  hooks
  const { value, setValue, handleInputChange } = useInput();

  // ID  state  render  useRef ( rerender)
  const id = useRef(1);

  //  render 
  const [todos, setTodos] = useState(() => {
    console.log("todos init");
    let todoData = window.localStorage.getItem("todos"); //(null)
    if (todoData) {
      todoData = JSON.parse(todoData);
      //  id  local 
      id.current = todoData[0].id + 1;
    } else {
      todoData = [];
    }
    return todoData;
  });
  console.log(todos)
  const [todosFilter, setTodosFilter] = useState(null);
  const [updatingTodo, setUpdatingTodo] = useState(null);
  const [updateValue, setUpdateValue] = useState(null);
  const editingInput = useRef(null);

  // useEffect ([] render  call API)

  //  todos()
  useEffect(() => {
    writeTodosToLocalStorage(todos);
    console.log("useEffect: todos", JSON.stringify(todos));
    // (return clean up function)
  }, [todos]);

  //  updatingTodo()
  useEffect(() => {
    //  DOM 
    if (updatingTodo) {
      editingInput.current.focus();
    }
  }, [updatingTodo]);

  const handleButtonAddTodo = useCallback(() => {
    setTodos([
      {
        //  useRef  current 
        id: id.current,
        content: value,
        isDone: false,
      },
      ...todos,
    ]);
    setValue("");
    //  useRef  current 
    id.current++;
  }, [setValue, todos, value]);

  const handleDeleteTodo = (id) => {
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  const handleToggleIsDone = (id) => {
    setTodos(
      todos.map((todo) => {
        //  ID 
        if (todo.id !== id) return todo;
        return {
          ...todo,
          //  ID 
          isDone: !todo.isDone,
        };
      })
    );
  };
  const handleUpdateClick = (e) => {
    setUpdateValue(e.target.innerText);
    setUpdatingTodo({
      id: e.target.getAttribute("data-todo-id"),
    });
  };

  const handleUpdateChange = (e) => {
    setUpdateValue(e.target.value);
    setTodos(
      todos.map((todo) => {
        //  ID 
        if (todo.id !== Number(e.target.getAttribute("data-todo-id")))
          return todo;
        return {
          ...todo,
          content: e.target.value,
        };
      })
    );
  };

  const handleClearDoneTodos = useCallback(() => {
    // 
    setTodos(todos.filter((todo) => !todo.isDone));
  }, [todos]);

  //
  return {
    todos,
    setTodos,
    id,
    handleButtonAddTodo,
    handleDeleteTodo,
    handleToggleIsDone,
    handleUpdateClick,
    updatingTodo,
    setUpdatingTodo,
    updateValue,
    handleUpdateChange,
    value,
    handleInputChange,
    todosFilter,
    setTodosFilter,
    handleClearDoneTodos,
    editingInput
  };
}
